.navbar-nav a {
  color: rgb(22, 78, 22);
  position: relative;
  text-decoration: none;
}

.navbar-nav a:hover {
  color: rgb(65, 153, 38);
}

.navbar-nav a::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1.5px;
  border-radius: 5px;
  bottom: 0;
  left: 0;
  background-color: rgb(65, 153, 38);
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
}

.navbar-nav a:hover::before {
  visibility: visible;
  transform: scaleX(1);
}

